import React from 'react'
import ReactDOM from 'react-dom'
import './assets/index.scss'
import App from './App'

import { HashRouter } from 'react-router-dom'
import { CookiesProvider } from 'react-cookie'

ReactDOM.render(
	<CookiesProvider>
		<HashRouter basename="/">
			<App />
		</HashRouter>
	</CookiesProvider>,
	document.getElementById('root')
)
